import firebaseApp from './firebase';
import * as firebase from 'firebase';

class WorldBank{
    constructor(){
        this.session = 0;
        this.initListener()
    }

    componentWillMount(){
        //this.checkCurrentUser()
        //this.nationListListener()
        //this.sessionListener()
        this.initListener()
    }

    sessionListener(){
        firebaseApp.database().ref().child('session').on('value', (snap)=> {
          this.setState({session: snap.val()})
          console.log('world bank set session ' + snap.val())
        })
    }

    initListener(){
        firebaseApp.database().ref().child('session').on('value', (snap) => {
            this.session = snap.val()
            console.log('world bank set session ' + snap.val())
        })
    }

    randomIDGenerator(){
        var id = ''
        for(var i = 0; i < 3; i++){
            var n = Math.floor(Math.random() * 1048576)
            id += n.toString(16) 
        }
        return id
    }

    async queueGenerator(){
        var q = (Math.floor(Math.random() * 65536)).toString(16)
        while(q.length < 4){
            q = '0' + q
        }
        var contain = false
        await firebaseApp.database().ref().child('loan/queue').once('value', (snap) => {
            if(snap.val() == null){
                return
            }
            var p = Object.keys(snap.val())
            p.forEach((key) => {
                if(key == q){
                    contain = true
                }
            })
        })
        if(contain){
            return this.queueGenerator()
        }else{
            return q
        }
    }

    async bankLoan(uid, amount, mortgage, period, interest){
        //uid : String
        //amount : double
        //mortgage : JSON with props(resource:String, index:int)
        //period : int
        //interest : double
        amount = parseInt(amount)
        var loan = {
            debtor: uid,
            creditor: 'WorldBank',
            amount: amount,
            pay: 0,
            mortgage: mortgage,
            startSession: this.session,
            endSession: this.session + period,
            interest: interest,
            state: 'ONGOING',
            timestamp: Date.now()
        }
        var id = this.randomIDGenerator()
        const sendLoan = await firebaseApp.database().ref().child('loan/loanData/'+id).transaction(function(post){
            post = loan
            return post
        })
        const addUser = await firebaseApp.database().ref().child('users/'+uid).transaction(function(post){
            if(post != null){
                if(post['historyData'] == null){
                    post['historyData'] = {}
                }
                if(!post['historyData'].hasOwnProperty('loan')){
                    post['historyData']['loan'] = {}
                }
                post['historyData']['loan'][id] = false
                post['resources']['Money'] += amount
                post['building'][mortgage['resource']][mortgage['index']]['mortgage'] = id
            }
            return post
        })
        return (sendLoan.committed && addUser.committed)
    }

    async debitLoan(uid, amount, mortgage, period, interest){
        //uid : String
        //amount : double
        //mortgage : JSON with props(resource:String, index:int)
        //period : int
        //interest : double
        console.log('debt')
        amount = parseInt(amount)
        var loan = {
            debtor: uid,
            amount: amount,
            mortgage: mortgage,
            period: period,
            interest: interest,
            state: 'QUEUE',
            timestamp: Date.now()
        }
        var id = await this.queueGenerator()
        const sendLoan = await firebaseApp.database().ref().child('loan/queue/'+id).transaction(function(post){
            post = loan
            return post
        })
        const addUser = await firebaseApp.database().ref().child('users/'+uid).transaction(function(post){
            if(post != null){
                if(post['historyData'] == null){
                    post['historyData'] = {}
                }
                if(!post['historyData'].hasOwnProperty('loanApply')){
                    post['historyData']['loanApply'] = {}
                }
                post['historyData']['loanApply'][id] = false
                post['building'][mortgage['resource']][mortgage['index']]['mortgage'] = id
            }
            return post
        })
        console.log('debt')
        return (sendLoan.committed && addUser.committed)
    }

    async creditLoan(uid, queueID){
        //uid : String
        //queueID : String
        var queueLoan
        await firebaseApp.database().ref().child('loan/queue/'+queueID).once('value', (snap) => {
            queueLoan = snap.val()
            console.log(snap.key)
        })
        console.log('after snaping')
        queueLoan['creditor'] = uid
        queueLoan['startSession'] = this.session
        queueLoan['endSession'] = this.session + queueLoan['period']
        queueLoan['pay'] = 0
        queueLoan['timestamp'] = Date.now()
        queueLoan['state'] = 'ONGOING'
        var id = queueID + this.randomIDGenerator()
        const deleteQueue = await firebaseApp.database().ref().child('loan/queue/'+queueID).transaction(function(post){
            post = null
            return post
        })
        const sendLoan = await firebaseApp.database().ref().child('loan/loanData/'+id).transaction(function(post){
            post = queueLoan
            return post
        })
        const addUserCredit = await firebaseApp.database().ref().child('users/'+uid).transaction(function(post){
            if(post != null){
                if(post['historyData'] == null){
                    post['historyData'] = {}
                }
                if(!post['historyData'].hasOwnProperty('loan')){
                    post['historyData']['loan'] = {}
                }
                post['historyData']['loan'][id] = false
                post['resources']['Money'] -= queueLoan['amount']//give money
            }
            return post
        })
        const addUserDebit = await firebaseApp.database().ref().child('users/'+queueLoan['debtor']).transaction(function(post){
            if(post != null){
                if(!post['historyData'].hasOwnProperty('loan')){
                    post['historyData']['loan'] = {}
                }
                post['historyData']['loanApply'][id] = null
                post['historyData']['loan'][id] = false
                post['resources']['Money'] += queueLoan['amount']//get money
                post['building'][queueLoan['mortgage']['resource']][queueLoan['mortgage']['index']]['mortgage'] = id
            }
            return post
        })
        console.log(sendLoan.committed + ', ' + sendLoan.committed + ', ' + addUserCredit.committed + ', ' + addUserDebit.committed)
        return (sendLoan.committed && sendLoan.committed && addUserCredit.committed && addUserDebit.committed)
    }

    async removeQueue(queueID, mortgage, debtor){
        const deleteQueue = await firebaseApp.database().ref().child('loan/queue/'+queueID).transaction(function(post){
            post = null
            return post
        })
        const setMortgage = await firebaseApp.database().ref().child('users/'+debtor+'/building/'+mortgage['resource']).transaction(function(post){
            if(post != null){
                console.log(post)
                for(var i = 0; i < post.length; i++){
                    if(post[i]['mortgage'] == queueID){
                        post[i]['mortgage'] = false
                    }
                }
            }
            return post
        })
    }

    async claimMortgage(loanID){
        console.log('sending mortgage request...')
        var transaction = await firebaseApp.database().ref().child('loan/claim/'+loanID).transaction(function(post){
            post = false
            return post
        })
        console.log(transaction.committed)
    }

}

var worldBank = new WorldBank()
export default worldBank