import React, { Component } from 'react';
import logo from '../logo.svg';
import './Home.css';

import { Button, Row, Col, OverlayTrigger, Popover, FormGroup, FormControl, ControlLabel, Tab, Tabs } from 'react-bootstrap';
import Dropdown from 'react-dropdown'
import 'react-dropdown/style.css'

//@material-ui/core
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import ButtonBase from '@material-ui/core/ButtonBase';

import {Master, initialTradeData} from '../Logic/master'
import firebaseApp from '../Logic/firebase';
import * as firebase from 'firebase';
import WorldBank from '../Logic/WorldBank'

class BankLoan extends Component {
  constructor(props){
    super(props);
    this.admin = firebaseApp.database().ref().child('admin')
    this.nation = {}
    this.nationOptions = []
    this.factoryListener = null
    this.loanListener = null
    this.worldBank = WorldBank
    this.state = { 
      nationChosen: null,
      loanList: [],//{key, value}
      factoryChosen: null,
      amount: 0,
      type: null,
      interest: 0,
      period: 0,
      session: 0,
      userData: false,
      rMatrix: {},
      tradeData: initialTradeData
    };
  }

  routePop(path){
    this.props.history.goBack();
  }

  componentWillMount(){
    console.log('mount')
    this.worldBank.initListener()
    this.checkCurrentUser()
    this.nationListListener()
    this.sessionListener()
  }

  sessionListener(){
    firebaseApp.database().ref().child('session').on('value', (snap)=> {
      this.setState({session: snap.val()})
    })
  }

  nationListListener(){
    const list = firebaseApp.database().ref().child('idToName')
    list.on('child_added', snap => {
      this.nationOptions.push({ value: snap.key, label: snap.val()['name'] })
      this.nation[snap.key] = snap.val()
    })
  }

  async checkCurrentUser(){
    firebaseApp.auth().onAuthStateChanged((user) => {
      if (user != null) {
        if(user.getIdTokenResult().then((idTokenResult) => {return idTokenResult.claims.admin})){
          this.setState({currentUser : user, login: true})
            console.log(user.email)
            user.getIdTokenResult().then((idTokenResult) => {
              console.log(idTokenResult.claims.admin)
          })
          console.log('We are authenticated now!') 
        }else{
          console.log('not admin')
          firebaseApp.auth().signOut()
        }
      }else{
        console.log('no user')
      }
    })
    console.log('login? '+this.state.login)
  }

  _onSelectNation = (value) => {
    console.log(JSON.stringify(value))
    this.setState({nationChosen: value, loanList: [], factoryChosen: null, amount: 0, type: null, interest: 0, period:0})
    this.loanListener = firebaseApp.database().ref().child('loan/loanData').orderByChild('debtor').equalTo(value['value']).once('value', (snap) => {
      console.log('listening')
      if(snap.val() != null){
        var k = Object.keys(snap.val())
        for(var i = 0; i < k.length; i++){
          var insert = false;
          console.log(k[i]+ '    ' + (snap.val()[k[i]]['state']=='FINISH').toString())
          if(snap.val()[k[i]]['creditor'] != 'WorldBank' || snap.val()[k[i]]['state'] == 'FINISH'){
            console.log(k[i] + ' skip')
            insert = true
          }
          if(!insert){
            for(var j = 0; j < this.state.loanList.length; j++){
              if(snap.val()[k[i]]['endSession'] < this.state.loanList[j]['value']['endSession']){
                this.state.loanList.splice(j, 0, {key: k[i], value: snap.val()[k[i]]})
                insert = true
                break
              }
            }
          }
          if(!insert){
            this.state.loanList.push({key: k[i], value: snap.val()[k[i]]})
          }
        }
        console.log(this.state.loanList)
      }
      this.setState({loanList: this.state.loanList})
      console.log(this.state)
    })
  }

  _onSelectFactory = (value) => {
    this.setState({factoryChosen: value})
  }

  selectMortgageView(){
    if(this.state.nationChosen != null){
      var factoryList = []
      const list = firebaseApp.database().ref().child('users/' + this.state.nationChosen['value'] + '/building')
      this.factoryListener = list.once('value', snap => {
        var resources = Object.keys(snap.val())
        resources.forEach((r) => {
          var factories = snap.val()[r]
          for(var i = 0; i < factories.length; i++){
            if(!factories[i]['mortgage']){
              factoryList.push({ value: JSON.stringify({resource:r, index:i, level:factories[i]['level']}), label: r + ' Production Line ' + (i+1).toString() + ' (level ' + (factories[i]['level']+1).toString() + ')' })
            }
          }
        })
      })
      return(
        <Row>
          <Col xs={2} md={2} lg={2}></Col>
          <Col xs={8} md={8} lg={8}>
            <Dropdown options={factoryList} onChange={this._onSelectFactory} value={this.state.factoryChosen} placeholder="Select an factory as mortgage" />
          </Col>
          <Col xs={2} md={2} lg={2}></Col>
        </Row>
      )
    }
  }

  _handleAmountChange = event => {
    this.setState({
      [event.target.id]: event.target.value
    });
  }

  enterAmountView(){
    if(this.state.factoryChosen != null){
      var factory = JSON.parse(this.state.factoryChosen.value)
      return(
        <Row>
          <Col xs={2} md={2} lg={2}></Col>
          <Col xs={8} md={8} lg={8}>
            <form onSubmit={null}>
              <FormGroup controlId="amount" bsSize="large">
                <ControlLabel>Amount</ControlLabel>
                <FormControl
                  value={this.state.amount}
                  onChange={this._handleAmountChange}
                  type="number"
                />
              </FormGroup>
            </form>
            <p>The mortgage value is {Master.building[factory.resource]['mortgageValue'][factory.level]}</p>
          </Col>
          <Col xs={2} md={2} lg={2}></Col>
        </Row>
      )
    }
  }

  chooseLoanTypeView(){
    if(this.state.amount > 0){
      return(
        <Row>
          <Col xs={2} md={2} lg={2}></Col>
          <Col xs={4} md={4} lg={4}>
            <Button
              block
              bsSize="large"
              className="Button"
              onClick={() => this.setState({type: 'Bank' })}
            >
              Loan From Bank
            </Button>
          </Col>
          <Col xs={4} md={4} lg={4}>
            <Button
              block
              bsSize="large"
              className="Button"
              onClick={() => this.setState({type: '3rdParty' })}
            >
              Loan From Other Nation
            </Button>
          </Col>
          <Col xs={2} md={2} lg={2}></Col>
        </Row>
      )
    }
  }

  getInterestA(){
    if(this.state.session < 3){
      return 4
    }else if(this.state.session < 4){
      return 5
    }else if(this.state.session < 5){
      return 4
    }else if(this.state.session < 7){
      return 6
    }else if(this.state.session < 8){
      return 7
    }else if(this.state.session < 9){
      return 7
    }else if(this.state.session < 10){
      return 5
    }else if(this.state.session < 11){
      return 4
    }else if(this.state.session < 12){
      return 3
    }else if(this.state.session < 13){
      return 7
    }else if(this.state.session < 14){
      return 7
    }else if(this.state.session < 15){
      return 10
    }else if(this.state.session < 16){
      return 8
    }else if(this.state.session < 17){
      return 5
    }else{
      return 3
    }
  }
  
  getInterestB(){
    if(this.state.session < 3){
      return 8
    }else if(this.state.session < 4){
      return 8
    }else if(this.state.session < 5){
      return 6
    }else if(this.state.session < 7){
      return 6
    }else if(this.state.session < 8){
      return 6
    }else if(this.state.session < 9){
      return 6
    }else if(this.state.session < 10){
      return 8
    }else if(this.state.session < 11){
      return 8
    }else if(this.state.session < 12){
      return 6
    }else if(this.state.session < 13){
      return 12
    }else if(this.state.session < 14){
      return 11
    }else if(this.state.session < 15){
      return 12
    }else if(this.state.session < 16){
      return 12
    }else if(this.state.session < 17){
      return 7
    }else{
      return 7
    }
  }

  getInterestC(){
    if(this.state.session < 3){
      return 15
    }else if(this.state.session < 4){
      return 15
    }else if(this.state.session < 5){
      return 12
    }else if(this.state.session < 7){
      return 11
    }else if(this.state.session < 8){
      return 14
    }else if(this.state.session < 9){
      return 12
    }else if(this.state.session < 10){
      return 7
    }else if(this.state.session < 11){
      return 6
    }else if(this.state.session < 12){
      return 10
    }else if(this.state.session < 13){
      return 15
    }else if(this.state.session < 14){
      return 15
    }else if(this.state.session < 15){
      return 18
    }else if(this.state.session < 16){
      return 16
    }else if(this.state.session < 17){
      return 16
    }else{
      return 3
    }
  }

  chooseInterest(){
    if(this.state.type != null){
      var interestComponent = function(){}
      if(this.state.type == 'Bank'){
        interestComponent = function(){
          var packageOptions = []
          packageOptions.push({ value: JSON.stringify({interest:this.getInterestA(), period:2}), label: 'Package A (Interest '+ this.getInterestA() +'% 2 Session)' })
          packageOptions.push({ value: JSON.stringify({interest:this.getInterestB(), period:4}), label: 'Package B (Interest '+this.getInterestB()+'% 4 Session)' })
          packageOptions.push({ value: JSON.stringify({interest:this.getInterestC(), period:6}), label: 'Package C (Interest '+this.getInterestC()+'% 6 Session)' })
          return(
            <Col>
              <Row>
                <p>Select Package Offer By Bank</p>
              </Row>
              <Row>
                <Dropdown options={packageOptions} onChange={this._bankInterest} value={this.state.bankPackage} placeholder="Select loan package" />
              </Row>
            </Col>
          )
        }.bind(this)
      }else{
        interestComponent = function(){
          return(
            <Row>
              <Col xs={6} md={6} lg={6}>
                <form onSubmit={null}>
                  <FormGroup controlId="interest" bsSize="large">
                    <ControlLabel>Interest</ControlLabel>
                    <FormControl
                      value={this.state.interest}
                      onChange={this._handleInterestChange}
                      type="number"
                    />
                  </FormGroup>
                </form>
              </Col>
              <Col xs={6} md={6} lg={6}>
                <form onSubmit={null}>
                  <FormGroup controlId="period" bsSize="large">
                    <ControlLabel>Period</ControlLabel>
                    <FormControl
                      value={this.state.period}
                      onChange={this._handleInterestChange}
                      type="number"
                    />
                  </FormGroup>
                </form>
              </Col>
            </Row>
            
          )
        }.bind(this)
      }
      return(
        <Row>
          <Col xs={2} md={2} lg={2}></Col>
          <Col xs={8} md={8} lg={8}>
            {interestComponent()}
          </Col>
          <Col xs={2} md={2} lg={2}></Col>
        </Row>
      )
    }
  }

  _bankInterest = (value) => {
    var ob = JSON.parse(value['value'])
    this.setState({bankPackage: value, interest: ob['interest'], period: ob['period']})
  }

  _handleInterestChange = event => {
    this.setState({
      [event.target.id]: event.target.value
    });
  }

  sendLoan(){
    if(this.state.interest > 0 && this.state.period > 0){
      return(
        <Row>
          <Col xs={4} md={4} lg={4}></Col>
          <Col xs={4} md={4} lg={4}>
            <Button bsStyle="success" bsSize="large" onClick={() => this.sendLoanE()}>SET LOAN</Button>
          </Col>
          <Col xs={4} md={4} lg={4}></Col>
        </Row>
      )
    }
  }

  async sendLoanE(){
    var result
    if(this.state.type == 'Bank'){
      result = await this.worldBank.bankLoan(this.state.nationChosen['value'], parseInt(this.state.amount), JSON.parse(this.state.factoryChosen['value']), parseInt(this.state.period), parseFloat(this.state.interest))
    }else{
      result = await this.worldBank.debitLoan(this.state.nationChosen['value'], parseInt(this.state.amount), JSON.parse(this.state.factoryChosen['value']), parseInt(this.state.period), parseFloat(this.state.interest))
    }
    if(result){
      alert('Success Setup Loan');
      //this.setState({nationChosen: null, factoryChosen: null, amount: 0, type: null, interest: 0, period: 0,})//will pop up
      this.routePop()
    }else{
      alert('Please send again!!!');
    }
  }

  renderList = (props) => {
    const data = props.data
    
    return (
      <div key={data.key}>
        <Paper className="ListTile">
          <Grid container spacing={16}>
            <Grid item xs={12} sm container>
              <Grid item xs container direction="column" spacing={16}>
                <Grid item xs>
                  <Typography gutterBottom variant="subtitle1" style={{ fontSize: 20 }}>
                    {this.nation[data.value['debtor']]['name']}
                  </Typography>
                  <Typography gutterBottom style={{ fontSize: 15 }}>{'End Session : '+data.value['endSession']}</Typography>
                  <Typography gutterBottom style={{ fontSize: 15 }}>{'Interest    : '+data.value['interest']}</Typography>
                  <Typography color="textSecondary" style={{ fontSize: 15 }}>{'Mortgage: ' + JSON.stringify(data.value['mortgage'])}</Typography>
                </Grid>
              </Grid>
              <Grid item>
                <Typography variant="subtitle1" style={{ fontSize: 18 }}>${data.value['amount']}</Typography>
                <Typography variant="subtitle1" style={{ fontSize: 15 }}>{'Paid: ' + data.value['pay'] + '%'}</Typography>
                <Typography variant="subtitle1" style={{ fontSize: 15 }}>{'Creditor : ' + data.value['creditor']}</Typography>
                <Grid item>
                {data.value['state'] == 'ongoing'? 
                  <Button bsStyle="success" bsSize="small" onClick={() => this.worldBank.claimMortgage(data.key)} disabled={this.state.session <= data.value['endSession']}>Claim Mortgage</Button>
                    : <Typography variant="subtitle1" style={{ fontSize: 15 }}>{data.value['state']}</Typography>
                }
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Paper>
      </div>
    );
  }

  render() {
    console.log('render YY ' + this.state.loanList.length)
    console.log(this.state)
    return (
      <div className="LoanApply">
        <Row>
          <Col xs={6} md={6} lg={6}>
            <Typography gutterBottom variant="subtitle1" style={{ fontSize: 20, color: '#444444'  }}>
              {this.state.nationChosen==null?'Choose A Nation':this.state.nationChosen['label']}
            </Typography>
            <div className="ListView">
              {
                this.state.loanList.map((data) => {
                  return <this.renderList data={data} key={data.key}/>
                })
              }                    
            </div>
          </Col>
          <Col xs={6} md={6} lg={6}>
            <Row>
              <Col>
                <p className='PageTitle'>LOAN</p>
              </Col>
            </Row>
            <Row>
              <Col xs={2} md={2} lg={2}></Col>
              <Col xs={8} md={8} lg={8}>
                <Dropdown options={this.nationOptions} onChange={this._onSelectNation} value={this.state.nationChosen} placeholder="Select an nation" />
              </Col>
              <Col xs={2} md={2} lg={2}></Col>
            </Row>
            {this.selectMortgageView()}
            {this.enterAmountView()}
            {this.chooseLoanTypeView()}
            {this.chooseInterest()}
            {this.sendLoan()}
          </Col>
        </Row>
      </div>
    );
  }
}

export default BankLoan;