import React, { Component } from 'react';
import logo from '../logo.svg';
import './Home.css';

import { Button, Grid, Row, Col, OverlayTrigger, Popover, FormGroup, FormControl, ControlLabel, Tab, Tabs } from 'react-bootstrap';
import Dropdown from 'react-dropdown'
import 'react-dropdown/style.css'

import {Master, initialTradeData} from '../Logic/master'
import firebaseApp from '../Logic/firebase';
import * as firebase from 'firebase';

class ThirdPartyLoan extends Component {
  
  constructor(props){
    super(props);
    this.admin = firebaseApp.database().ref().child('admin')
    this.nationOptions = []
    this.state = { 
    nationChosen: null,
    session: 0,
    userData: false,
    rMatrix: {},
    tradeData: initialTradeData
    };
  }

  routePop(path){
    this.props.history.goBack();
  }

  componentWillMount(){
    this.checkCurrentUser()
    this.nationListListener()
  }

  nationListListener(){
    const list = firebaseApp.database().ref().child('idToName')
    list.on('child_added', snap => {
      this.nationOptions.push({ value: snap.key, label: snap.val()['name'] })
    })
  }

  async checkCurrentUser(){
    firebaseApp.auth().onAuthStateChanged((user) => {
      if (user != null) {
        if(user.getIdTokenResult().then((idTokenResult) => {return idTokenResult.claims.admin})){
          this.setState({currentUser : user, login: true})
            console.log(user.email)
            user.getIdTokenResult().then((idTokenResult) => {
              console.log(idTokenResult.claims.admin)
          })
          console.log('We are authenticated now!') 
        }else{
          console.log('not admin')
          firebaseApp.auth().signOut()
        }
      }else{
        console.log('no user')
      }
    })
    console.log('login? '+this.state.login)
  }

  _onSelectNation = (value) => {
    if(value != this.state.nationChosen1)
      this.setState({nation: [], rules: [], contracting: false})
    this.setState({nationChosen1: value})
  }

  render() {
    return (
      <div className="App">
        <Row>
          <Col>
            <p className=''>LOAN</p>
          </Col>
          <Col>
            <Dropdown options={this.nationOptions} onChange={this._onSelectNation} value={this.state.nationChosen} placeholder="Select an option" />
          </Col>
        </Row>
      </div>
    );
  }
}

export default ThirdPartyLoan;