import React, { Component } from 'react';
import logo from '../logo.svg';
import './Home.css';

import { Button, Row, Col, OverlayTrigger, Popover, FormGroup, FormControl, ControlLabel, Tab, Tabs } from 'react-bootstrap';
import Dropdown from 'react-dropdown'
import 'react-dropdown/style.css'
import ReactList from 'react-list';

//@material-ui/core
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import ButtonBase from '@material-ui/core/ButtonBase';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';

import {Master, initialTradeData} from '../Logic/master'
import firebaseApp from '../Logic/firebase';
import * as firebase from 'firebase';
import worldBank from '../Logic/WorldBank';

function Transition(props) {
    return <Slide direction="up" {...props} />;
}

class Home extends Component {

    constructor(props){
        super(props);
        this.admin = firebaseApp.database().ref().child('admin')
        //for contract
        this.nation = {}
        this.nationOptions = []
        this.bankLoan = {
            ongoing: [],
            finish: []
        }
        this.thirdPartyLoan = {
            ongoing: [],
            queue: []
        }
        this.state = { 
            //third party dialog
            dialogOpen: false,
            loanChosen: null,
            nationChosen: null,
            people: 0,
            //rule
            email: "",
            password: "",
            login: false,
            session: 0,
            userData: false,
            rMatrix: {},
            tradeData: initialTradeData
        };
    }

    routePush(path){
        this.props.history.push(path);
    }

    componentWillMount(){
        this.checkCurrentUser()
        this.nationListListener()
        this.sessionListener()
    }

    sessionListener(){
        firebaseApp.database().ref().child('session').on('value', (snap)=> {
          this.setState({session: snap.val()})
        })
    }

    nationListListener(){
        const list = firebaseApp.database().ref().child('idToName')
        list.on('child_added', snap => {
            this.nationOptions.push({ value: snap.key, label: snap.val()['name'] })
            this.nation[snap.key] = snap.val()
        })
    }

    _onSelectNation = (value) => {
        console.log(JSON.stringify(value))
        this.setState({nationChosen: value, loanList: [], factoryChosen: null, amount: 0, type: null, interest: 0, period:0})
        this.loanListener = firebaseApp.database().ref().child('loan/loanData').orderByChild('debtor').equalTo(value['value']).once('value', (snap) => {
          console.log('listening')
          if(snap.val() != null){
            var k = Object.keys(snap.val())
            for(var i = 0; i < k.length; i++){
              var insert = false;
              console.log(k[i]+ '    ' + (snap.val()[k[i]]['state']=='FINISH').toString())
              if(snap.val()[k[i]]['creditor'] != 'WorldBank' || snap.val()[k[i]]['state'] == 'FINISH'){
                console.log(k[i] + ' skip')
                insert = true
              }
              if(!insert){
                for(var j = 0; j < this.state.loanList.length; j++){
                  if(snap.val()[k[i]]['endSession'] < this.state.loanList[j]['value']['endSession']){
                    this.state.loanList.splice(j, 0, {key: k[i], value: snap.val()[k[i]]})
                    insert = true
                    break
                  }
                }
              }
              if(!insert){
                this.state.loanList.push({key: k[i], value: snap.val()[k[i]]})
              }
            }
            console.log(this.state.loanList)
          }
          this.setState({loanList: this.state.loanList})
          console.log(this.state)
        })
      }

    loanListener(){
        console.log('LOAN LISTENER START')
        const loanData = firebaseApp.database().ref().child('loan/loanData')
        //const worldBankLoan = loanData.orderByChild('creditor').equalTo('WorldBank')
        loanData.on('child_added', (snap) => {
            console.log('LOAN DETECT')
            if(snap.val()['creditor']=='WorldBank'){
                if(snap.val()['state'] == 'ONGOING'){
                    var insert = false
                    for(var i = 0; i < this.bankLoan.ongoing.length; i++){
                        if(this.bankLoan.ongoing[i].val()['endSession'] > snap.val()['endSession']){
                            this.bankLoan.ongoing.splice(i, 0, snap)
                            insert = true
                            break
                        }
                    }
                    if(!insert){
                        this.bankLoan.ongoing.push(snap)
                    }
                }else{
                    var insert = false
                    for(var i = 0; i < this.bankLoan.finish.length; i++){
                        if(this.bankLoan.finish[i].val()['fTimestamp'] < snap.val()['fTimestamp']){
                            this.bankLoan.finish.splice(i, 0, snap)
                            insert = true
                            break
                        }
                    }
                    if(!insert){
                        this.bankLoan.finish.push(snap)
                    }
                }
            }else{
                if(snap.val()['state'] == 'ONGOING'){
                    var insert = false
                    for(var i = 0; i < this.thirdPartyLoan.ongoing.length; i++){
                        if(this.thirdPartyLoan.ongoing[i].val()['endSession'] > snap.val()['endSession']){
                            this.thirdPartyLoan.ongoing.splice(i, 0, snap)
                            insert = true
                            break
                        }
                    }
                    if(!insert){
                        this.thirdPartyLoan.ongoing.push(snap)
                    }
                }
            }
            this.setState({})
        })
        loanData.on('child_removed', (snap) => {
            if(snap.val()['creditor']=='WorldBank'){
                if(snap.val()['state'] == 'ONGOING'){
                    for(var i = 0; i < this.bankLoan.ongoing.length; i++){
                        if(this.bankLoan.ongoing[i].key == snap.key){
                            this.bankLoan.ongoing.splice(i, 1)
                            break
                        }
                    }
                }else{
                    for(var i = 0; i < this.bankLoan.finish.length; i++){
                        if(this.bankLoan.finish[i].key == snap.key){
                            this.bankLoan.finish.splice(i, 1)
                            break
                        }
                    }
                }
            }else{
                if(snap.val()['state'] == 'ONGOING'){
                    for(var i = 0; i < this.thirdPartyLoan.ongoing.length; i++){
                        if(this.thirdPartyLoan.ongoing[i].key == snap.key){
                            this.thirdPartyLoan.ongoing.splice(i, 1)
                            break
                        }
                    }
                }
            }
            this.setState({})
        })
        const queue = firebaseApp.database().ref().child('loan/queue')
        queue.on('child_added', (snap)=>{
            var insert = false
            for(var i = 0; i < this.thirdPartyLoan.queue.length; i++){
                if(this.thirdPartyLoan.queue[i].val()['timestamp'] > snap.val()['timestamp']){
                    this.thirdPartyLoan.queue.splice(i, 0, snap)
                    insert = true
                    break
                }
            }
            if(!insert){
                this.thirdPartyLoan.queue.push(snap)
            }
            console.log('Queue  :  ' + snap.key)  
            this.setState({})   
        })
        queue.on('child_removed', (snap)=>{
            for(var i = 0; i < this.thirdPartyLoan.queue.length; i++){
                if(this.thirdPartyLoan.queue[i].key == snap.key){
                    this.thirdPartyLoan.queue.splice(i, 1)
                    break
                }
            }
            this.setState({})
        })
    }

    async checkCurrentUser(){
        firebaseApp.auth().onAuthStateChanged(async function(user){
            if (user != null) {
                var token
                await user.getIdTokenResult().then((idTokenResult) => {
                    token = idTokenResult.claims.admin
                    console.log('Token ' + token)
                })
                if(token){
                    this.setState({currentUser : user, login: true})
                    console.log(user.email)
                    console.log('We are authenticated now!') 
                    this.loanListener()
                }else{
                    console.log('not admin')
                    alert('Only admin can login')
                    firebaseApp.auth().signOut()
                }
            }else{
                console.log('no user')
            }
        }.bind(this))
        console.log('login? '+this.state.login)
    }

    validateLoginForm() {
        return this.state.email.length > 0 && this.state.password.length > 0;
    }
    
    handleLoginChange = event => {
        this.setState({
            [event.target.id]: event.target.value
        });
    }
    
    handleLoginSubmit = async(event) => {
        event.preventDefault();
        await this.login()
    }
    
    async login(){
        console.log('LOGIN');//('waixiong1997@hotmail.com', '1997@hotmail.com')
        firebaseApp.auth().signInWithEmailAndPassword(this.state.email, this.state.password).then((user)=>{
            user.user.getIdTokenResult().then((idTokenResult) => {
                console.log('Token is '+idTokenResult.claims.admin)
            })
        }).then(function(){
            //any listener
            //this.loanListener()
        }.bind(this));
    }
    
    logout(){
        firebaseApp.auth().signOut()
        console.log('Sign Out')
        this.setState({login: false})
    }

    renderList = (props) => {
        const snap = props.snap
        
        return (
            <div key={snap.key}>
                <Paper className="ListTile">
                    <Grid container spacing={16}>
                        <Grid item xs={12} sm container>
                            <Grid item xs container direction="column" spacing={16}>
                                <Grid item xs>
                                    <Typography gutterBottom variant="subtitle1" style={{ fontSize: 20 }}>
                                        {this.nation[snap.val()['debtor']]['name']}
                                    </Typography>
                                    <Typography gutterBottom style={{ fontSize: 15 }}>{'End Session : '+snap.val()['endSession']}</Typography>
                                    <Typography gutterBottom style={{ fontSize: 15 }}>{'Interest    : '+snap.val()['interest']}</Typography>
                                    <Typography color="textSecondary" style={{ fontSize: 15 }}>{'Mortgage: ' + JSON.stringify(snap.val()['mortgage'])}</Typography>
                                </Grid>
                            </Grid>
                            <Grid item>
                                <Typography variant="subtitle1" style={{ fontSize: 18 }}>${snap.val()['amount']}</Typography>
                                <Typography variant="subtitle1" style={{ fontSize: 15 }}>{'Paid: ' + snap.val()['pay'] + '%'}</Typography>
                                <Typography variant="subtitle1" style={{ fontSize: 15 }}>{'Creditor : ' + snap.val()['creditor']}</Typography>
                                <Grid item>
                                    {snap.val()['state'] == 'ONGOING'? 
                                        <Button bsStyle="success" bsSize="small" onClick={() => worldBank.claimMortgage(snap.key).then(this.setState({}))} disabled={this.state.session <= snap.val()['endSession'] && snap.val()['creditor'] == 'WorldBank'}>Claim Mortgage</Button>
                                        : <Typography variant="subtitle1" style={{ fontSize: 15 }}>{snap.val()['state']}</Typography>
                                    }
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Paper>
            </div>
        );
    }

    renderQueueList = (props) => {
        const snap = props.snap
        return (
            <div key={snap.key}>
                <Paper className="ListTile">
                    <Grid container spacing={16}>
                        <Grid item xs={12} sm container>
                            <Grid item xs container direction="column" spacing={16}>
                                <Grid item xs>
                                    <Typography gutterBottom variant="subtitle1" style={{ fontSize: 20 }}>
                                        {'ID : ' + snap.key}
                                    </Typography>
                                    <Typography gutterBottom style={{ fontSize: 15 }}>{this.nation[snap.val()['debtor']]['name']}</Typography>
                                    <Typography gutterBottom style={{ fontSize: 15 }}>{'Period : '+snap.val()['period']}</Typography>
                                    <Typography gutterBottom style={{ fontSize: 15 }}>{'Interest    : '+snap.val()['interest']}</Typography>
                                    <Typography color="textSecondary" style={{ fontSize: 15 }}>{'Mortgage: ' + JSON.stringify(snap.val()['mortgage'])}</Typography>
                                </Grid>
                            </Grid>
                            <Grid item>
                                <Typography variant="subtitle1" style={{ fontSize: 15 }}>${snap.val()['amount']}</Typography>
                                <Grid item>
                                    <Button bsStyle="success" bsSize="small" onClick={() => this.handleClickDialogOpen(snap.key)}>GIVE LOAN</Button>
                                    <Button bsStyle="warning" bsSize="small" onClick={() => worldBank.removeQueue(snap.key, snap.val()['mortgage'], snap.val()['debtor'])}>CANCEL LOAN</Button>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Paper>
            </div>
        );
    }

    //Dialog for third
    handleClickDialogOpen = (loanID) => {
        this.setState({ dialogOpen: true, loanChosen: loanID });
    };
    
    handleDialogClose = () => {
        this.setState({ dialogOpen: false, loanChosen: null, nationChosen: null });
    };

    _onSelectNation = (value) => {
        console.log(JSON.stringify(value))
        this.setState({nationChosen: value})
    }
    //Dialog for third

    _handlePeople = event => {
        console.log(event.target.id)
        this.setState({
            [event.target.id]: event.target.value
        });
    }

    async addPeople() {
        const add = await firebaseApp.database().ref().child('users').child(this.state.nationChosen['value']).transaction(function(post){
            if(post != null){
                post['human'] += parseInt(this.state.people)
                post['humanAvailable'] += parseInt(this.state.people)
                post['resources']['Money'] -= parseInt(this.state.people) * 100
                post['GDP']['govern'] += parseInt(this.state.people) * 100
            }
            return post
        }.bind(this))
        if(add.committed){
            alert('Success')
        }else{
            alert('Error')
        }
    }

    render() {
        return (
            <div className="App">
                {this.state.login? <Tabs defaultActiveKey={1} id="uncontrolled-tab-example" className="Tab">
                    <Tab eventKey={1} title="Loan From Bank">
                        <div className="App-header">
                            <Row>
                                <Button bsStyle="primary" bsSize="large" onClick={() => this.logout()}>LOGOUT</Button>
                            </Row>
                            <Row>
                                <Dropdown options={this.nationOptions} onChange={this._onSelectNation} value={this.state.nationChosen} placeholder="Select an nation" />
                            </Row>
                            <Row>
                                <Col xs={6} md={6} lg={6}>
                                    <form onSubmit={null}>
                                        <FormGroup controlId="people" bsSize="large">
                                            <ControlLabel>people</ControlLabel>
                                            <FormControl
                                                value={this.state.people}
                                                onChange={this._handlePeople}
                                                type="number"
                                            />
                                        </FormGroup>
                                    </form>
                                </Col>
                            </Row>
                            <Row>
                                <Button bsStyle="primary" bsSize="large" onClick={() => this.addPeople()}>Add People</Button>
                            </Row>
                        </div>
                    </Tab>
                    <Tab eventKey={3} title={"Session "+this.state.session.toString()} disabled>
                        Tab 3 content
                    </Tab>
                </Tabs> : <div className="Login">
                    <form onSubmit={this.handleLoginSubmit} className="LoginForm">
                        <FormGroup controlId="email" bsSize="large">
                            <ControlLabel>Email</ControlLabel>
                            <FormControl
                                autoFocus
                                type="email"
                                value={this.state.email}
                                onChange={this.handleLoginChange}
                            />
                        </FormGroup>
                        <FormGroup controlId="password" bsSize="large">
                            <ControlLabel>Password</ControlLabel>
                            <FormControl
                                value={this.state.password}
                                onChange={this.handleLoginChange}
                                type="password"
                            />
                        </FormGroup>
                        <Button
                            block
                            bsSize="large"
                            disabled={!this.validateLoginForm()}
                            type="submit"
                            className="LoginButton"
                        >
                            Login
                        </Button>
                    </form>
                </div>}
            </div>
        );
    }
}

export default Home;
